*, :before, :after {
  box-sizing: border-box;
}

* {
  font: inherit;
  margin: 0;
  padding: 0;
}

ul[role="list"], ol[role="list"] {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

html, body {
  height: 100%;
}

body {
  min-height: 100vh;
  text-rendering: optimizespeed;
  line-height: 1.5;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img, picture, svg {
  max-width: 100%;
  height: auto;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *, :before, :after {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

a {
  outline: none;
  text-decoration: none;
}

button {
  background-color: #0000;
  border: none;
  margin: 0;
  padding: 0;
}

:root {
  --color-background: #f6f6f6;
  --color-header: #fff;
  --color-header-border: #111;
  --color-list-category: #1119;
  --color-selected-category: #4f2ee8;
  --color-slider: #e0e0e0;
  --color-main-font: #111;
  --color-purple-btn: #4f2ee8;
  --color-yellow-btn: #eac645;
  --color-black-btn: #111;
  --color-purple-title: #4f2ee8;
  --color-author-font: #b4afaf;
  --color-shoping-border: #4f2ee866;
  --color-modal-background: #1116;
}

.darkmode {
  --color-background: #202024;
  --color-header: #111;
  --color-header-border: #fff;
  --color-list-category: #fff9;
